import React, {useState} from "react";
import './App.css';
import IconButton from "@material-ui/core/IconButton";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Menu from "@material-ui/core/Menu";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

function App() {
    const [showAllCamera, setShowAllCamera] = useState("true")
    const removeCamera =  ()=>{
        setShowAllCamera(!showAllCamera)
    }
    return (

        <div className="App">
            <AppBar position="static">
                <Toolbar>
                    <IconButton edge="start" color="inherit" aria-label="menu">
                        <Menu/>
                    </IconButton>
                    <Typography variant="h6">
                        News
                    </Typography>
                    <Button color="inherit">Login</Button>
                </Toolbar>
            </AppBar>
            <div className={"check"}>
                <div className={"camera1"}>
                    <iframe width="488px" height="488px" src="https://www.youtube.com/embed/1EiC9bvVGnk"
                            title="YouTube video player" frameBorder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen></iframe>

                </div>
                {showAllCamera&& <div className={"camera1"}>
                    <iframe width="488px" height="488px" src="https://www.youtube.com/embed/jbqT0fTj088"
                            title="YouTube video player"
                            frameBorder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen></iframe>

                </div>}

            </div>
            <button onClick={removeCamera}>remove/add second camera</button>
        </div>

    );
}

export default App;
